
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import UploadImg from 'base/uploadimg'
import { hasClass } from 'common/js/dom'
import { cityt } from 'common/js/chd'
export default{
data () {
return {
height: 200,
formatDemoValue: ['01'],
formatValue: ['01'],
format: function (value) {
return `${value}`
},
switch6: false,
commonList: [ '原创文章', '转载文章' ],
radioValue: ['原创文章'],
items: 1,
imglist: [],
interest: [],
inname: [],
chdudata: [],
city: []
}
},
components: {
XTextarea,
UploadImg,
XInput,
XButton,
Group,
Cell,
PopupPicker,
Picker,
XSwitch,
Checklist
},
created () {
},
mounted () {
setTimeout(() => {
this.gutinterest()
this.getchengdudata()
this.getinit()
}, 500)
},
methods: {
change (value, label) {
console.log('change:', value, label)
},
getinit () {
let _url = 'http://peicentapi.demo.sclonsee.com/v1/index/wx-config'

let _url_ = window.location.href
this.$http.get(_url, {params: {url: _url_}}).then(response => {
let config = response.data.data
this.$wechat.config(config)
}, response => {
alert(response)
})
},
getchengdudata () {
this.chdudata = cityt
},
gutinterest () {
this.$http.get('http://peicentapi.demo.sclonsee.com/v1/news/news-interest').then(response => {
let hotinterest = response.data.data
hotinterest.map((item, index) => {
this.interest.push({
name: item.name,
value: item.id,
parent: 0
})
})
console.log(this.interest)
}, response => {
alert(response)
})
},
add () {
console.log(this.items)
this.items ++
},
push () {
for (let i = 0; i < this.items; i++) {
let imglist = this.$refs.contentimg[i].$vnode.elm.children[0].children
let textlist = this.$refs.contenttext[i]._data.currentValue
let imgerl = []
for (let a = 0; a < imglist.length; a++) {
if (!hasClass(imglist[a], 'uploader-box')) {
imgerl.push(imglist[a].children[0].src)
}
}
let obj = {
id: i + 1,
imgrul: imgerl,
text: textlist
}
this.imglist.push(obj)
}
console.log(this.inname)
}
}
}



