export default {
  data () {
    return {
      list: [],
      allnum: ''
    }
  },
  props: {
    fromindex: {
      type: String,
      default: ''
    },
    num: {
      type: Number,
      default: 15
    },
    page: {
      type: Number,
      default: 1
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    godetails (item) {
      let timestamp = Date.parse(new Date())
      this.$router.push({ path: `/zixundetails/${timestamp}/${item.id}/${item.attribute.id}` })
    },
    editzixun (item) {
      console.log(item)
    },
    delzixun (item) {
      console.log(item)
    }
  }
}



