<template>
  <div class="container">
    <sticky :offset="46">
      <tab :line-width=3 >
        <tab-item :selected="top_index === item"  v-for="(item, index) in listtop" @on-item-click="democlcik(index)" :key="index">{{item}}</tab-item>
      </tab>
    </sticky>
    <scroller ref="scroll" class="scroll"
        :data="list"
        :pulldown="pulldown"
        :pullup="pullup"
        @pulldown="pulldowndata"
        @scrollToEnd="pullupdata"
        :refreshDelay="refreshDelay">
      <div class="scroll-content">
        <activity :data="list" :fromindex="fromindex"></activity>
        <div class="alldata" v-show="nodata">我是有底线的</div>
      </div>
    </scroller>
    <add :url="url"></add>
  </div>
</template>

<script>
import { Tab, TabItem, Sticky, Divider, XButton } from 'vux'
import scroller from 'base/scroller'
import activity from 'base/activity'
import add from 'base/add'
export default{
  components: {
    scroller,
    activity,
    Tab,
    TabItem,
    Sticky,
    Divider,
    XButton,
    add
  },
  data () {
    return {
      listtop: ['我的发布', '我的参与'],
      top_index: '我的发布',
      index: 0,
      url: 'push_help',
      page: 1,
      all: 15,
      list: [],
      pulldown: true,
      pullup: true,
      nodata: false,
      refreshDelay: 500,
      topnum: 0,
      fromindex: 'mine'
    }
  },
  created () {
    setTimeout(() => {
      this._loadgetData()
    }, 30)
  },
  methods: {
    _loadgetData () {
      this.$http.get('http://peicentapi.demo.sclonsee.com/v1/activity/index', {params: { target_id: this.$route.params.id, page: this.page, flag: 3, from: 0 }}).then(response => {
        this.list = this.list.concat(response.data.data)
        if (response.data.data.length > 0) {
          this.all = this.list.length
        } else {
          this.nodata = true
        }
        console.log(this.list)
      }, response => {
        alert(response)
      })
    },
    _loadjionData () {
      this.$http.get('http://peicentapi.demo.sclonsee.com/v1/activity/my-join', {params: { member_id: this.$route.params.id, page: this.page, flag: 3, from: 0 }}).then(response => {
        this.list = this.list.concat(response.data.data)
        if (response.data.data.length > 0) {
          this.all = this.list.length
        } else {
          this.nodata = true
        }
        console.log(this.list)
      }, response => {
        alert(response)
      })
    },
    pullupdata () {
      if (!this.nodata) {
        this.page ++
        if (this.topnum === 1) {
          this._loadjionData()
        } else {
          this._loadgetData()
        }
      } else {
        this.nodata = true
      }
    },
    pulldowndata () {
      this.page = 1
      this.list = []
      this._loadData()
    },
    democlcik (demo) {
      this.page = 1
      this.list = []
      this.topnum = demo
      if (demo === 1) {
        this._loadjionData()
      } else {
        this._loadgetData()
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '~vux/src/styles/1px.less';
@import '~vux/src/styles/center.less';
.container{
  position: fixed;
  width: 100%;
  top: 0;
  bottom: 0;
}
.scroll{
  height: 100%;
  overflow: hidden;
}
.scroll-content{
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-bottom: 100px;
}
.alldata{
  text-align: center;
  background-color: #dedede;
}     
.box {
  padding: 15px;
}
.active-6-1 {
  color: rgb(252, 55, 140) !important;
  border-color: rgb(252, 55, 140) !important;
}
.active-6-2 {
  color: #04be02 !important;
  border-color: #04be02 !important;
}
.active-6-3 {
  color: rgb(55, 174, 252) !important;
  border-color: rgb(55, 174, 252) !important;
}
.tab-swiper {
  background-color: #fff;
  height: 100px;
}
</style>



