
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'


export default {
data () {
return {
allmine: [
{
name: '我的资讯',
classname: 'my_information',
url: './my_information',
icon: require('../assets/img/icon_dt_2x.png')
},
{
name: '我的互助',
classname: 'my_help',
url: './my_help',
icon: require('../assets/img/icon_hzhu_2x.png')
},
{
name: '我的预订',
classname: 'my_order',
url: './my_order',
icon: require('../assets/img/icon_dzuo_2x.png')
},
{
name: '我的机构',
classname: 'my_mechanism',
url: './my_mechanism',
icon: require('../assets/img/icon_jigou_2x.png')
},
{
name: '我的店铺',
classname: 'my_store_list',
url: './my_store_list',
icon: require('../assets/img/icon_dp_2x.png')
},
{
name: '我的活动',
classname: 'my_activity',
url: './my_activity',
icon: require('../assets/img/icon_hd_2x.png')
},
{
name: '我的商品',
classname: 'my_goods',
url: './my_goods',
icon: require('../assets/img/icon_sp_2x.png')
},
{
name: '我要投稿',
classname: 'my_tougao',
url: './my_tougao',
icon: require('../assets/img/icon_zxgl_2x.png')
},
{
name: '我要推广',
classname: 'my_extension',
url: './my_extension',
icon: require('../assets/img/icon_tg_2x.png')
}
],
mid: ''
}
},
components: {
Grid,
GridItem,
GroupTitle
},
methods: {
onItemClick () {
console.log('on item click')
},
go (item, index) {
let storage = window.localStorage
this.mid = storage.getItem('uid')
if (index === 7) {
this.$router.push({path: `${item.url}/3/${this.mid}`})
} else {
this.$router.push({path: `${item.url}/1/${this.mid}`})
}
}
}
}



