
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import scroller from 'base/scroller'
import zixun from 'base/zixun'
export default{
components: {
scroller,
zixun,
Tab,
TabItem,
Sticky,
Divider,
XButton
},
data () {
return {
listtop: [],
index: 0,
url: 'push_help',
page: 1,
all: 15,
list: [],
pulldown: true,
pullup: true,
nodata: false,
refreshDelay: 500,
cat_id: 0,
geturl: 'http://peicentapi.demo.sclonsee.com/v1/news/index'
}
},
created () {
this._loadinterest()



},
methods: {
_loadinterest () {
this.$http.get('http://peicentapi.demo.sclonsee.com/v1/member/member-interest', {params: { member_id: 9 }}).then(response => {
this.listtop = response.data.data
this.cat_id = this.listtop[0].news_cat_id
this._loadData()
}, response => {
alert(response)
})
},
_loadData () {
this.$http.get(this.geturl, {params: { cat_id: this.cat_id, page: this.page, flag: 3, province_id: 51, city_id: 510100000000, region_id: 510104000000, street_id: 510104020000, community_id: 659004502528 }}).then(response => {
this.list = this.list.concat(response.data.data)
if (response.data.data.length > 0) {
this.all = this.list.length
} else {
this.nodata = true
}
console.log(this.list)
}, response => {
alert(response)
})
},
pullupdata () {
if (!this.nodata) {
this.page ++
this._loadData()
} else {
this.nodata = true
}
},
pulldowndata () {
this.page = 1
this.list = []
this._loadData()
},
slect (item) {
this.page = 1
this.list = []
this.cat_id = item.news_cat_id
this._loadData()
}
}
}



