
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import scroller from 'base/scroller'
import order from 'base/order'
export default{
components: {
scroller,
order,
Tab,
TabItem,
Sticky,
Divider,
XButton
},
data () {
return {
listtop: ['待使用', '进行中', '待评价', '已完成', '全部'],
top_index: '全部',
index: 4,
url: 'push_help',
page: 1,
all: 15,
list: [],
pulldown: true,
pullup: true,
nodata: false,
refreshDelay: 500,
geturl: 'http://peicentapi.demo.sclonsee.com/v1/help/index'
}
},
created () {
setTimeout(() => {
this._loadData()
}, 30)
},
methods: {
_loadData () {
this.$http.get(this.geturl, {params: {member_id: this.$route.params.id, page: this.page, flag: 3}}).then(response => {
this.list = this.list.concat(response.data.data)
if (response.data.data.length > 0) {
this.all = this.list.length
} else {
this.nodata = true
}
console.log(this.list)
}, response => {
alert(response)
})
},
pullupdata () {
if (!this.nodata) {
this.page ++
this._loadData()
} else {
this.nodata = true
}
},
pulldowndata () {
this.page = 1
this.list = []
this._loadData()
},
democlcik (demo) {
if (demo === 1) {
this.geturl = 'http://peicentapi.demo.sclonsee.com/v1/help/my-join'
} else {
this.geturl = 'http://peicentapi.demo.sclonsee.com/v1/help/index'
}
this.page = 1
this.list = []
this._loadData()
console.log(demo)
}
}
}



