
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import scroller from 'base/scroller'
import activity from 'base/activity'
import add from 'base/add'
export default{
components: {
scroller,
activity,
Tab,
TabItem,
Sticky,
Divider,
XButton,
add
},
data () {
return {
listtop: ['我的发布', '我的参与'],
top_index: '我的发布',
index: 0,
url: 'push_help',
page: 1,
all: 15,
list: [],
pulldown: true,
pullup: true,
nodata: false,
refreshDelay: 500,
topnum: 0,
fromindex: 'mine'
}
},
created () {
setTimeout(() => {
this._loadgetData()
}, 30)
},
methods: {
_loadgetData () {
this.$http.get('http://peicentapi.demo.sclonsee.com/v1/activity/index', {params: { target_id: this.$route.params.id, page: this.page, flag: 3, from: 0 }}).then(response => {
this.list = this.list.concat(response.data.data)
if (response.data.data.length > 0) {
this.all = this.list.length
} else {
this.nodata = true
}
console.log(this.list)
}, response => {
alert(response)
})
},
_loadjionData () {
this.$http.get('http://peicentapi.demo.sclonsee.com/v1/activity/my-join', {params: { member_id: this.$route.params.id, page: this.page, flag: 3, from: 0 }}).then(response => {
this.list = this.list.concat(response.data.data)
if (response.data.data.length > 0) {
this.all = this.list.length
} else {
this.nodata = true
}
console.log(this.list)
}, response => {
alert(response)
})
},
pullupdata () {
if (!this.nodata) {
this.page ++
if (this.topnum === 1) {
this._loadjionData()
} else {
this._loadgetData()
}
} else {
this.nodata = true
}
},
pulldowndata () {
this.page = 1
this.list = []
this._loadData()
},
democlcik (demo) {
this.page = 1
this.list = []
this.topnum = demo
if (demo === 1) {
this._loadjionData()
} else {
this._loadgetData()
}
}
}
}



