
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
data () {
return {
imgurl: [],
demo06_index: 0,
imglist: []
}
},
created () {
this.$http.get('http://peicentapi.demo.sclonsee.com/v1/index/ads', {position_id: 3, region_id: 510107000, type: '', size: ''}).then(response => {
this.imglist = response.data.data
}, response => {
alert(response)
})
},
mounted () {
setTimeout(() => {
this.getimg()
}, 500)
},
methods: {
demo06_onIndexChange (index) {
this.demo06_index = index
},
getimg () {
this.imgurl = this.imglist.map((item, index) => ({
img: item.thumb[0]
}))
}
},
components: {
Swiper,
SwiperItem,
GroupTitle,
XButton,
Divider
}
}



