import scroller from 'base/scroller'
import allhead from 'base/head'
import jigou from 'base/jigou'
export default {
  data () {
    return {
      listtop: [{
        id: '',
        name: '全部'
      }],
      cat_id: '',
      list: [],
      fromindex: 'dating',
      pulldown: true,
      pullup: true,
      nodata: false,
      refreshDelay: 500,
      page: 1
    }
  },
  created () {
    this._loadinterest()
  },
  methods: {
    _loadinterest () {
      this.$http.get('http://peicentapi.demo.sclonsee.com/v1/organization/catgory').then(response => {
        this.listtop = this.listtop.concat(response.data.data)
        console.log(this.listtop)
        this._loadData()
      }, response => {
        alert(response)
      })
    },
    _loadData () {
      this.$http.get('http://peicentapi.demo.sclonsee.com/v1/organization/organizations', {params: { flag: 1, cat_id: this.cat_id, page: this.page, province_id: 51, city_id: 510100000000, region_id: 510104000000, street_id: 510104020000, community_id: 510104020007, status: 2, lat: 30.591, long: 104.0502 }}).then(response => {
        this.list = this.list.concat(response.data.data)
        if (response.data.data.length > 0) {
          this.all = this.list.length
        } else {
          this.nodata = true
        }
        console.log(this.list)
      }, response => {
        alert(response)
      })
    },
    pullupdata () {
      if (!this.nodata) {
        this.page ++
        this._loadData()
      } else {
        this.nodata = true
      }
    },
    pulldowndata () {
      this.page = 1
      this.list = []
      this._loadData()
    },
    slect (item) {
      this.page = 1
      this.list = []
      this.cat_id = item.id
      this._loadData()
    }
  },
  components: {
    allhead, jigou, scroller
  }
}



