
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import { cityt } from 'common/js/chd'
export default {
components: {
PopupPicker,
Group,
Picker,
XButton,
Divider,
Cell,
XSwitch
},
mounted () {
setTimeout(() => {
this.getchengdudata()
}, 500)
},
methods: {
onhide (val) {
this.zaitype = val
if (this.zaitype) {
console.log(1)
let cparentid = this.parentid
console.log(cparentid)
this.$http.get('http://peicentapi.demo.sclonsee.com/v1/index/area', {params: {parent_id: cparentid}}).then(response => {
let childrendata = response.data.data
console.log(childrendata)
childrendata.map((item, index) => {
this.children.push({
name: item.label,
value: item.value,
parent: 0
})
})
setTimeout(() => {
this.zshowPopupPicker = true
}, 500)
}, response => {
alert(response)
})
}
},
onChange (val) {
console.log('val change', val)
this.parentid = val[3]


},
onChangea (val) {
console.log('val change', val)
this.address = this.$refs.picker2.getNameValues()
},
getchengdudata () {
this.chdudata = cityt
}
},
data () {
return {
title4: '地址',
chdudata: [],
address: '...',
showPopupPicker: false,
zshowPopupPicker: false,
zaitype: false,
parentid: '',
children: []
}
}
}



