import scroller from 'base/scroller'
import allhead from 'base/head'
import activity from 'base/activity'
export default {
  data () {
    return {
      listtop: [{
        id: '',
        name: '全部'
      }],
      cat_id: '',
      list: [],
      fromindex: 'dating',
      pulldown: true,
      pullup: true,
      nodata: false,
      refreshDelay: 500,
      page: 1
    }
  },
  created () {
    this._loadData()
  },
  methods: {
    _loadData () {
      this.$http.get('http://peicentapi.demo.sclonsee.com/v1/activity/index', {params: { flag: 1, page: this.page, province_id: 51, city_id: 510100000000, region_id: 510104000000, street_id: 510104020000, community_id: 659004502528, from: '' }}).then(response => {
        this.list = this.list.concat(response.data.data)
        if (response.data.data.length > 0) {
          this.all = this.list.length
        } else {
          this.nodata = true
        }
        console.log(this.list)
      }, response => {
        alert(response)
      })
    },
    pullupdata () {
      if (!this.nodata) {
        this.page ++
        this._loadData()
      } else {
        this.nodata = true
      }
    },
    pulldowndata () {
      this.page = 1
      this.list = []
      this._loadData()
    }
  },
  components: {
    allhead, scroller, activity
  }
}



